  --medium-breakpoint-up: 40em;
  --large-breakpoint-up: 64em;
  --xlarge-breakpoint-up: 87.5em;
  --small-breakpoint-down: 39.9375em;
  --medium-breakpoint-down: 63.9375em;
  --large-breakpoint-down: 87.4375em;
}

.has-fade {
  opacity: 0;
  visibility: hidden;
}

@keyframes fade-in {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
    visibility: visible;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

@keyframes slide-in {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.slide-in {
  animation: slide-in 10secs ease-in-out forwards;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Public Sans", sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  line-height: 1.3;
  overflow-x: hidden;
}

@media (min-width: var(--large-breakpoint-up)) {
  body {
    font-size: 1.125rem;
  }
}
body.noscroll {
  overflow: hidden;
}

p {
  line-height: 1.5;
  margin-bottom: 2.25rem;
}

.container {
  max-width: 75rem;
  margin: 0 auto;
  overflow: hidden;
}

.container--pHeader {
  padding-right: 6rem;
  padding-left: 6rem;
  padding-top: 1rem;
  padding-bottom: 2rem;
}

@media (min-width: var(--large-breakpoint-up)) {
  .container--pHeader {
    padding-right: 6rem;
    padding-left: 6rem;
    padding-top: 1.5rem;
    padding-bottom: 2.5rem;
  }
}
.container--pContent {
  padding-right: 6rem;
  padding-bottom: 2rem;
  padding-left: 6rem;
}

@media (min-width: var(--large-breakpoint-up)) {
  .container--pContent {
    padding-right: 6rem;
    padding-left: 6rem;
  }
}
.flex {
  display: flex;
}

.flex-jc-sb {
  justify-content: space-between;
}

.flex-ai-c {
  align-items: center;
}

.flex-jc-c {
  justify-content: center;
}

.btn {
  background-color: #2fa8cc;
  color: #f4f4f4;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 14px 40px;
  font-size: 16px;
  cursor: pointer;
}

@media (max-width: var(--medium-breakpoint-down)) {
  .hide-for-mobile {
    display: none;
  }
}
@media (min-width: var(--large-breakpoint-up)) {
  .hide-for-desktop {
    display: none;
  }
}
.hidden {
  display: none;
}

.visible {
  display: block;
}

.tabs {
  background-color: gray;
  display: block;
  transition: all 500ms ease-in-out;
  height: 4rem;
  position: fixed;
  top: 0px;
  width: 100%;
}

.top-container {
  background-color: #f1f1f1;
  padding: 1rem;
  text-align: center;
}

.header {
  z-index: 1;
  width: 100%;
  background-color: hsl(233, 8%, 62%);
  transition: all 500ms ease-in-out;
  height: 4rem;
}

@media (min-width: var(--large-breakpoint-up)) {
  .header {
    height: 5rem;
  }
}
.header.open {
  width: 100%;
}

.header.open .header__toggle > span:first-child {
  transform: rotate(42deg);
}

.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}

.header.open .header__toggle > span:last-child {
  transform: rotate(-42deg);
}

.header.close {
  width: 100%;
}

.header.close .header__toggle > span:first-child {
  transform: rotate(0deg);
}

.header.close .header__toggle > span:nth-child(2) {
  opacity: 1;
}

.header.close .header__toggle > span:last-child {
  transform: rotate(0deg);
}

.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(hsl(233, 26%, 24%), transparent);
}

.header__menu {
  position: fixed;
  width: calc(100% - 2rem);
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  background-color: white;
  background-image: linear-gradient(hsl(136, 65%, 51%), transparent);
  margin-top: 1.5rem;
  padding: 1.625rem;
  border-radius: 5px;
}

.header__menu a {
  display: block;
  padding: 0.625rem;
  color: hsl(233, 26%, 24%);
  text-align: center;
}

.header__toggle > span {
  display: block;
  width: 40px;
  height: 5px;
  background-color: red;
  transition: all 300ms ease-in-out;
  transform-origin: 5px 2px;
}

.header__toggle > span:not(:last-child) {
  margin-bottom: 5px;
}

.disable-transition {
  transition: none !important;
}

.sticky {
  position: sticky;
  top: 0;
  width: 100%;
}

.content {
  padding: 1rem;
}

.carouselItem {
  flex: 0 0 100%;
  padding: 10px; /* Add spacing between items */
}

@media (min-width: var(--medium-breakpoint-up)) {
  .carouselItem {
    flex: 0 0 33.3333333333%; /* Set the width to one-third of the container */
    padding: 10px; /* Add spacing between items */
  }
}
@media (min-width: var(--large-breakpoint-up)) {
  .carouselItem {
    flex: 0 0 20%; /* Set the width to one-third of the container */
    padding: 10px; /* Add spacing between items */
  }
}
.carouselHeader {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px; /* Add some space between header and image */
  overflow-x: auto;
}

.carouselImg {
  display: flex;
  width: 100%; /* Take up the full width of the parent container */
  height: auto;
  overflow-x: auto;
}

.carouselArrows {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px; /* Add some spacing between carousel and arrows */
}

.carouselArrows button {
  font-size: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.carouselArrows button:hover {
  color: #007bff; /* Change color on hover (optional) */
